import Api from '@/api/api'

function getMessage(data) {
    return Api().get('message', data)
}


function storeMessage(data) {
    return Api().post('message/create', data)
}

function updateMessage(data) {
    return Api().put('/message/update', data)
}

function deleteMessage(data) {
    return Api().delete('message/delete', data)
}

export {
    getMessage ,
    storeMessage ,
    updateMessage,
    deleteMessage
}