<template>
  <section class="announcement">
    <!-- title -->
    <div class="text-center mb-2">
      <div class="d-flex align-items-center justify-content-center mb-2">
        <b-avatar class="mr-1" size="40" variant="light-danger">
          <img :src="require('@/assets/icons/building_icon.svg')" alt="" draggable="false" width="30" />
        </b-avatar>
        <h4 class="m-0">اطلاعیه ها</h4>
      </div>
      <span>لطفا برای دریافت صورتحساب ابتدا نوع آن را مشخص کنید </span>
    </div>

    <!-- loader -->
    <div v-if="loading" class="d-flex flex-column justify-content-center align-items-center" style="margin-top: 150px">
      <img :src="require('@/assets/images/logo/logo.svg')" alt="Logo" width="70" />
      <b-spinner style="margin-top: 10px; width: 3rem; height: 3rem" variant="primary" />
    </div>

    <main class="float-content" v-if="!loading">
      <div>
        <b-card v-for="(item, key) in getMessage" :key="key">
          <div class="d-flex align-items-center justify-content-between mb-1">
            <!-- left side -->
            <div class="d-flex align-items-center">
              <b-avatar class="avatar" size="35" style="background: #e0f9fc">
                <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                  <path
                    d="M12.0002 18.0738C15.3572 18.0738 18.0785 15.3524 18.0785 11.9954C18.0785 8.63847 15.3572 5.91711 12.0002 5.91711C8.64323 5.91711 5.92188 8.63847 5.92188 11.9954C5.92188 15.3524 8.64323 18.0738 12.0002 18.0738Z"
                    stroke="#00CFE8"
                    stroke-width="1.5"
                    stroke-linecap="round"
                    stroke-linejoin="round"
                  />
                  <path
                    d="M13.5932 10.0838C13.5932 10.0838 12.9358 9.35091 11.8695 9.44249C10.8032 9.53408 10.2892 10.1892 10.2892 10.8461C10.2892 12.7621 13.5932 11.4343 13.5932 13.399C13.5932 14.4119 11.4015 15.1269 10.0957 13.9353"
                    stroke="#00CFE8"
                    stroke-width="1.5"
                    stroke-linecap="round"
                    stroke-linejoin="round"
                  />
                  <path d="M12 4L12 6.89256" stroke="#00CFE8" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round" />
                  <path d="M12 17.1074L12 20" stroke="#00CFE8" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round" />
                  <path d="M20 12L17.1074 12" stroke="#00CFE8" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round" />
                  <path d="M6.89258 12L4.00002 12" stroke="#00CFE8" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round" />
                </svg>
              </b-avatar>
              <h5 class="mb-0">{{ item.message_title }}</h5>
            </div>

            <!-- right side -->
            <div class="d-flex align-content-">
              <!-- date -->
              <div class="d-flex align-items-center justify-content-center">
                <p class="m-0" style="font-size: 10px; color: #a5a5a5; margin-left: 8px !important">{{ jdate(item.created_at) }}</p>
                <img :src="require('@/assets/icons/calendar-icon.svg')" alt="bill-debt" draggable="false" />
              </div>

              <!-- edit -->
              <div class="cursor-pointer mx-1" @click="editAddAnnouncement(item)">
                <edit-icon size="15" />
              </div>

              <!-- del -->
              <div class="cursor-pointer">
                <img :src="require('@/assets/icons/trash.svg')" alt="bill-debt" draggable="false" @click="remove(item)" />
              </div>
            </div>
          </div>

          <!-- text -->
          <p v-html="item.message_body"></p>
        </b-card>
      </div>

      <b-button block variant="primary" @click="editAddAnnouncement(null)">
        <img :src="require('@/assets/icons/add_icon.svg')" alt="add" draggable="false" width="24" class="add-icon" />
        ثبت اطلاعیه جدید
      </b-button>
    </main>
  </section>
</template>

<script>
import AddIcon from "@core/components/svg-icons/AddIcon.vue";
import { BCard, BAvatar, BButton, BSpinner } from 'bootstrap-vue'
import ToastificationContent from "@core/components/toastification/ToastificationContent.vue"
import EditIcon from "@/@core/components/svg-icons/EditIcon.vue";
import { getMessage, deleteMessage } from "/src/api/messageServices";
import helper from "@/services/general"

export default {
  name: "announcement",
  mixins: [helper],
  components: {
    BCard,
    BAvatar,
    EditIcon,
    AddIcon,
    BButton,
    ToastificationContent,
    BSpinner
  },
  data() {
    return {
      getMessage: [],
      getTime: "",
      loading: false,
    }
  },
  methods: {
    async getInfo() {
      //** run loading */
      this.loading = true
      let { data } = await getMessage();
      this.getMessage = data.data.data;

      //** end loading */
      this.loading = false
    },

    editAddAnnouncement(item) {
      this.$store.commit("announcement/setAnnouncement", item)
      this.$router.push({ name: `edit-add-announcement`, replace: true })
    },

    /**confirm remove and remove */
    async remove(item) {
      this.$bvModal
        .msgBoxConfirm("آیا از حذف این اطلاعیه مطمئن هستید ؟", {
          title: 'هشدار',
          size: 'sm',
          okVariant: 'primary',
          okTitle: 'بله',
          cancelTitle: 'خیر',
          cancelVariant: 'outline-secondary',
          hideHeaderClose: true,
          centered: true,
        })
        .then(async (confirm) => {
          if (confirm) {
            try {
              await deleteMessage({ data: { id: item.id } })
              this.$toast({
                component: ToastificationContent,
                props: {
                  title: 'حذف با موفقیت انجام شد',
                  icon: 'CheckCircle',
                  variant: 'success',
                },
              })

              this.getInfo()
            } catch (error) {
              console.error(error)
            }
          }
        })
    },
  },
  async mounted() {
    this.getInfo()
  },
}
</script>

